import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DriverRfidCrudComponent } from './driver-rfid-crud/driver-rfid-crud.component';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
import { TABS_DEMO_ROUTES } from './driver-rfid-crud/routes';

const routes: Routes = [
  {
    path: "crud", component: DriverRfidCrudComponent, canActivate: [AuthGuard], children: TABS_DEMO_ROUTES
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DriverRfidRoutingModule { }
